import myHeader from "../../components/header.vue"
export default{
    name:"index",
    data(){
        return{
            chatList:[],
            chatValue:"",
            marginTop:"",
        }
    },
    components:{
        myHeader,
    },
    created(){
        this.headerLoad();
        this.load();
    },
    methods:{
        headerLoad(){
            this.title = "日志";
        },
        load(){
            this.$axios.get('static/data.json').then((res) => {
                this.chatList = res.data.chat;
                // console.log(this.chatList);
            },err => {
                 console.log('数据加载失败')
            });
        },
        marginHeight(){
            let height= this.$refs.elememt.offsetHeight-440;
            this.marginTop = "margin-top:-" + height + "px"
        },
        marginWidth(){
            let width= this.$refs.elementInput.offsetWidth;
            console.log(width);
        },
        transmit(){
            if(!this.chatValue){
                return;
            }
            var CId = this.chatList[this.chatList.length-1].cid + 1
            var promise = new Promise(
                    (reslove,reject)=>{
                        this.chatList.push({cid:CId,name:"Jerry",msg:this.chatValue});
                        this.chatValue = "";
                        reslove();
                    }
                );
            promise.then(()=>{
                this.marginHeight();
            });
        },
        
    }
}



