import myHeader from "../../components/header.vue"
export default{
    name:"index",
    data(){
        return{
            title:""
        }
    },
    components:{
        myHeader,
    },
    created(){
        this.headerLoad();
        this.load();
    },
    methods:{
        headerLoad(){
            this.title = "相册";
        },
    }

}



