import myHeader from "../../components/header.vue"

export default{
    name:"index",
    data(){
        return{
            title:"",
            // 轮播图
            carouselList:[],
            i:0,
            // 轮播图下方小圆点颜色
            carouselIdColor:"carousel-id-color",
            // 轮播图左右按钮是否显现
            btnclass:{
                opacity_off:true,
                opacity_on:false,
            },
            // 点击后导航栏下边框
            borderBottom:"border-bottom",
            // 导航栏
            navbarList:[],
            // 导航栏对应文字
            // 导航栏下方文字内容是否显示
            displayNone:"block_off",
            displayBlock:"block_on",
            navTitleList:[],
            advertisingList:[],
            rankingList:[],
            actColor:"",
            widthUl:"",
            styleUl:""
        }
    },
    components:{
        myHeader,
    },
    created(){
        this.load();
        this.headerLoad();
    },
    methods:{
        load(){
           this.$axios.get('static/data.json').then((res) => {
                console.log(res.data);
                this.carouselList = res.data.carouselList;
                this.navbarList = res.data.navbarList;
                this.navTitleList = res.data.navTitleList;
                this.advertisingList = res.data.advertisingList;
                this.rankingList = res.data.rankingList;
                this.widthUl = parseInt(this.carouselList.length) + "00%";
                this.styleUl = "width:"+this.widthUl+";margin-left:-0%";
                console.log(this.styleUl);
            },err => {
                console.log('数据加载失败')
            });
        },
        headerLoad(){
            this.title = "首页";
        },
        // 轮播图顶部小圆点
        carousel(index){
            this.i = index;
            for(var num=0; num<this.carouselList.length; num++){
                this.carouselList[num].isfouce = false;
            }
            this.carouselList[index].isfouce = true;
        },
        // 左右侧点击按钮
        clickBtn(a){
            if((this.i >0 &&this.i<this.carouselList.length-1)||(this.i==0&&a==1)||(this.i==this.carouselList.length-1&&a==-1)){
                this.i += a;
            }else if(this.i==0&&a==-1){
                this.i = this.carouselList.length-1;
            }else if (this.i==this.carouselList.length-1 && a==1) {
                this.i = 0;
            }
            for(var num=0; num<this.carouselList.length; num++){
                this.carouselList[num].isfouce = false;
            }
            this.carouselList[this.i].isfouce = true;
            this.styleUl = "width:"+this.widthUl+";margin-left:-"+this.i+"00%";
            console.log(this.styleUl);
        },
        // 是否选中轮播图
        bg_on(){
            this.btnclass.opacity_on=true;
            this.btnclass.opacity_off=false;
          },
        bg_off(){
          this.btnclass.opacity_on=false;
          this.btnclass.opacity_off=true;
        },
        // 是否选中该标题
        blockOn(index,key){
            for(var i=0; i<this.navTitleList[index].text.length; i++){
                this.navTitleList[index].text[i].isblock = false;
            }
            this.navTitleList[index].text[key].isblock = true;
        },
        blockOff(index,key){
            for(var i=0; i<this.navTitleList[index].text.length; i++){
                this.navTitleList[index].text[i].isblock = false;
            }
            this.navTitleList[index].text[key].isblock = true;
        },
        handleClick(index){
            for(var i=0; i<this.navbarList.length; i++){
                this.navbarList[i].isNavBlock = false;
            }
            this.navbarList[index].isNavBlock = true;
        },
        button(){
            console.log(this.datas);
        },
    },
}



